@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
    text-align: center;
}

section {
    position: relative;
    width: 100%;
    height: 100%;
}

.full-slide-bg {
    /* The image used */
    /* background-image: url("https://raw.githubusercontent.com/mukherjeearnab/photography/backend/images/FormatFactoryIMG_5662.jpg"); */

    /* Full height */
    height: 100vh;
    width: 100%;

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.photo-box {
    height: 100vh;
    width: 100%;
    object-fit: cover;
    overflow: hidden;
    background-position: center;
    position: relative;
    /* border-radius: 10px; */
}

/* ***************************************************************
   * Animation Slide Styling
   ***************************************************************/

.cb-slideshow,
.cb-slideshow:after {
    width: 100%;
    height: 100%;
    margin: auto;
    z-index: 0;
}

.cb-slideshow {
    list-style-type: none;
}

.cb-slideshow li span {
    margin: auto;
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    display: block;
    color: transparent;
    background-size: cover;
    background-position: 50% 50%;
    opacity: 0;
    transition: 2s;
}

.landing-content {
    color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 9999;
}

.landing-content h1 {
    font-family: "Playfair Display", serif;
    font-size: 3em;
    font-weight: 200;
}

.landing-content p {
    font-family: "JetBrains Mono", monospace;
    font-size: 1.5em;
    font-weight: 500;
}

.landing-instagram a {
    font-size: 1.5em;
    color: #fff;
    text-decoration: none;
}

.gallery-content {
    padding-top: 80px;
    padding-bottom: 80px;
    padding-left: 4%;
    padding-right: 4%;
    display: inline-block;
}

.gallery-content h1 {
    font-family: "JetBrains Mono", monospace;
    font-size: 1.5em;
    font-weight: 500;
    text-decoration: underline;
    padding: 0.5em;
}

.footer {
    padding: 2em;
    text-align: center;
}

.footer p {
    font-family: "JetBrains Mono", monospace;
    font-weight: 500;
}

.footer-heart {
    color: red;
}

.footer p a {
    text-decoration: underline;
    color: #666;
}

